import React from "react";

import "../node_modules/@fortawesome/fontawesome-free/css/all.min.css";
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import img1 from "./dhi2.jpg";
import img2 from "./dhi2.jpg";
import img3 from "./dhi2.jpg";
import img4 from "./dhi2.jpg";
import img5 from "./dhi2.jpg";
import img6 from "./dhi2.jpg";
import img7 from "./dhi2.jpg";
import img8 from "./dhi2.jpg";
const useStyles = makeStyles({
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});
export const Games = () => {
  const classes = useStyles();
  return (
    <div className="container">
    <div className="row">
      <div className="col-md-4">
    <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img1}
            title="Modern Ticketing"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Trip Analysis Using Modern Ticketing
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
             The digital solution for monitoring and analysing crowd of passengers in each route and provides a comfortable
             travel experience by allocating more bus in the crowded area and reducing the running of empty bus 
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/TRIP-ANALYSIS-USING-MODERN-TICKETING"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
      <div className="col-md-4">
      <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img2}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Railway Reservation System
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
             The efficient way of booking the tickets using oops concept.The tickets are allocated based on priority,availability,re-allocation of seats once the
             seat is empty after certain stations.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{marginTop:"3rem"}}>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/Railway-Reservation-System"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
      <div className="col-md-4">
      <Card className={classes.card} style={{marginTop:"3rem",height:"26rem"}}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={img3}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
             Bomberman 
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              It is multi-player game which contains the path from start to end with some obstacles and moving ghosts.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{marginTop:"4rem"}}>
          <div className="social-icons" >
                    <a
                      href="https://github.com/ARAVINDH17/Bomber-Man-Game"
                      target="_blank" 
                    >
                      <i className="fab fa-github"/>
                    </a>
                  </div>
        </CardActions>
      </Card>
      </div>
    </div>
    </div>
  );
};